import Services from "./Services";

export class AuthService extends Services {
  constructor(init) {
    super(init);
    this._name = "AUTH";
    return this;
  }

  //LOGIN ----------------------------------------------------------------------
  /**
   * @function login - log user to platform
   * @param {Object} data
   * @returns
   */
  login = async (data) => {
    return await this.decorate(
      async () =>
        await this.axios("/auth/login", {
          method: "POST",
          data,
        })
    );
  };

  //REGISTER ----------------------------------------------------------------------
  /**
   * @function register - Register user into platform
   * @param {Object} data
   * @returns
   */
  register = async (data) => {
    return await this.decorate(
      async () =>
        await this.axios(`/auth/register`, {
          method: "POST",
          data,
        })
    );
  };

  //CONFIRM USER ----------------------------------------------------------------------
  /**
   * @function confirm - Confirm user in to the  platform
   * @param {Object} data
   * @returns
   */

  confirm = async (data) => {
    return await this.decorate(
      async () =>
        await this.axios(`/auth/confirm`, {
          method: "POST",
          data,
        })
    );
  };

  //RESET USER ----------------------------------------------------------------------
  /**
   * @function reset - Register user from the platform platform
   * @param {Object} data
   * @returns
   */

  reset = async (data) => {
    return await this.decorate(async () => {
      await this.axios(`/auth/reset`, {
        method: "POST",
        data,
      });
    });
  };

  //PASSWORD RESET REQUEST ----------------------------------------------------------------------
  /**
   * @function password_reset - Reset user password
   * @param {Object} data
   * @returns
   */

  password_reset = async (data) => {
    return await this.decorate(async () => {
      await this.axios(`/auth/reset/request`, {
        method: "POST",
        data,
      });
    });
  };

  //SEND OTP ----------------------------------------------------------------------
  /**
   * @function send_otp - Send otp to verify user
   * @param {Object} dat
   * @returns
   */

  send_otp = async (data) => {
    return await this.decorate(async () => {
      await this.axios(`/auth/otp`, {
        method: "POST",
        data,
      });
    });
  };

  //VERIFY OTP ----------------------------------------------------------------------
  /**
   * @function send_otp - Register user otp
   * @param {Object} dat
   * @returns
   */

  verify_otp = async (data) => {
    return await this.decorate(async () => {
      await this.axios(`/auth/verify`, {
        method: "POST",
        data,
      });
    });
  };
}
export default AuthService;
