import Services from "./Services";

class OrderService extends Services {
  constructor(init) {
    super(init);
    this._name = "ORDER";
    return this;
  }

  //FIND ----------------------------------------------------------------------
  /**
   * @function login - log user to platform
   * @param {Object} data
   * @returns
   */
  find = async (params) => {
    return await this.decorate(
      async () =>
        await this.axios("order", {
          method: "GET",
          params,
        })
    );
  };
}

export default OrderService;
